import TodoView from "./views/TodoView";
import Day from "./views/Day";

const routes =
    [
        {
            name: 'home',
            path: '/',
            component: TodoView
        },
        {
            name: 'day-night',
            path: '/day',
            component: Day
        }
    ]



export default routes


