import React, { Component } from 'react';
import Todo from '../../components/Todo';

class TodoView extends Component {
    render() {
        return (
            <div className='container'>
                <h1><code>Todo</code></h1>
                <Todo />
            </div>
        );
    }
}

export default TodoView;



