import { TweenMax, Elastic } from 'gsap';


const duration = 0.5;

export default {
    show(target, cb) {
        return TweenMax
        .from(target, duration, {
            opacity: 0,
            height: 0,
            onComplete() {
                cb();
            },
            ease: Elastic.easeOut.config(0.25, 1),
        });
    },
    hide(target, cb) {
        return TweenMax
        .to(target, duration, {
            opacity: 0,
            height: 0,
            padding: 0,
            margin: 0,
            onComplete() {
                cb();
            },
            ease: Elastic.easeIn.config(0.25, 1),
        });
    },
    rise(target, cb) {
        return TweenMax
        .from(target, duration, {
            y: 1000,
        })
    },
    beat(target, type) {
        return TweenMax
        .fromTo(target, 1.5, {
            boxShadow: `0px 0px 5px 0px ${type==='sun'? 'yellow': 'white'}`,
        }, {
            boxShadow: `0px 0px 5px 30px transparent`,
            repeat: -1,
            delay: 1.5,
        })
    }
    

};


