import React from 'react';
import './style.css';
import animation from '../../animations';
import { Transition, TransitionGroup } from 'react-transition-group';


class Circle extends React.Component {

    componentDidMount() {
        animation.beat(this.circle.current, this.props.type)
    }

    circle = React.createRef();

    render() {
        const {props} = this;
        return (
            <Transition
                onEnter={e => {
                    animation.rise(e, () => { })
                }}
                onExit={() => {

                }}
                timeout={500}
                {...props}>
                <div ref={this.circle} className={`circle ${props.type}`}></div>
            </Transition>
        )
    }
}


const Sun = (props) => <Circle type='sun' {...props} />
const Moon = (props) => <Circle type='moon' {...props} />

class Day extends React.Component {
    state = {
        day: true
    }

    toogleDay = () => {
        this.setState({ day: !this.state.day })
    }

    render() {
        return (
            <div className={`container ${this.state.day ? 'day' : 'night'}`}>
                <TransitionGroup component='div'>
                    {
                        this.state.day ? <Sun /> : <Moon />
                    }
                </TransitionGroup>
                <button onClick={this.toogleDay}>toggle time</button>
            </div>
        )
    }
}


export default Day;


